import Interpol from 'interpol-js';

export let lowPulse = 0;
export let highPulse = 0;

Interpol.tween()
  .from(100)
  .to(200)
  .duration(3333)
  .ease(Interpol.easing.easeInOutCubic)
  .step(val => lowPulse = val * 10)
  .complete(function() { this.reverse(); })
  .start();



