import './styles.css';
import React from 'react';
import { map } from 'ramda';
import Menu from 'svg/menu';
import More from 'svg/more';
import Facebook from 'svg/facebook';
import Linkedin from 'svg/linkedin';
import Github from 'svg/github';
import Soundcloud from 'svg/soundcloud';

const social = [
  { name: 'facebook', href: 'www.facebook.com', icon: () => <Facebook/> },
  { name: 'linkedin', href: 'www.facebook.com', icon: () => <Linkedin/> },
  { name: 'github', href: 'www.facebook.com', icon: () => <Github/> },
  { name: 'soundcloud', href: 'www.facebook.com', icon: () => <Soundcloud/> }
];

const SocialIcon = ({ name, icon: Icon, href }) => <a key={name} href={href}><Icon/></a>;


export default () =>
  <div className="menu__container">
    <More/>
    {map(SocialIcon, social)}
  </div>;



