import { scene } from 'objects/common/scene';
import createGeom from 'helpers/createGeom';
import material from 'materials/bunny';
import { TimelineMax, TweenMax, TweenLite } from 'gsap';
import bunny from 'bunny';

const geometry = createGeom(bunny, { flat: true });
const mesh = new THREE.Mesh(geometry, material);
mesh.scale.x = 20;
mesh.scale.y = 20;
mesh.scale.z = 20;

// scene.add(mesh);


var tw = { x: 0 }

TweenMax.to(tw, 2, {x: 1000, repeat: -1, yoyo: true, ease: TweenMax.Elastic.easeInOutCubic })

export default () => {
  material.uniforms.time.value = tw.x * 0.001;
  // console.log(tw.x.toFixed(0))

  // material.position.z = -test
};



