import React, { Component } from 'react';
import { render } from 'react-dom';
import { createHashHistory } from 'history';
import { Router, Route, useRouterHistory } from 'react-router';
import { observer } from 'mobx-react';
import { Home } from './views';
import Menu from 'components/Menu';
import * as Interpol from 'helpers/intervals';
import update from 'helpers/update';
import state from 'state';
import { TweenMax } from 'gsap';

let tick = 0;

@observer
class Root extends Component {
  render() {
    return (
      <div>
        <Menu/>
        <div>{React.cloneElement(this.props.children, state)}</div>
      </div>
    );
  }
}


let lowPulse = { x: 1000 };
TweenMax.to(lowPulse, 2, { x: 2000, repeat: -1, yoyo: true, ease: TweenMax.Elastic.easeInOut });


const loop = () => {
  requestAnimationFrame(loop)
  update({
    tick: tick++,
    lowPulse: lowPulse.x,
    highPulse: Interpol.highPulse,
    state: state
  })
};

loop();
render(
	<Router history={useRouterHistory(createHashHistory)({ queryKey: false })} onUpdate={() => window.scrollTo(0, 0)}>
		<Route component={Root}>
			<Route path="/" component={Home} />
		</Route>
	</Router>,
	document.getElementById('app')
);



