import { scene } from 'objects/common/scene';
import material from 'materials/move';
import { TimelineMax, TweenMax, TweenLite } from 'gsap';

const tri = 10000 * 3 * 3;

const vertices = new Float32Array(tri);
const colors = new Uint8Array(tri);

for ( let i = 0; i < tri; i += 1 ) {
  vertices[i] = Math.random() - 0.5;
}

for ( let i = 0; i < tri; i += 9) {
  colors[i] = 155;
}
let mesh
const geometry = new THREE.BufferGeometry();
geometry.addAttribute('position', new THREE.BufferAttribute(vertices, 3));
geometry.addAttribute('color', new THREE.BufferAttribute(colors, 3, true));

for (var i = 0; i < 1; i++) {
  mesh = new THREE.Mesh(geometry, material);
  mesh.scale.x = 100
  mesh.scale.y = 100
  mesh.scale.z = 100
  // mesh.position.x = Math.random() * i * 2;
  // mesh.position.y = Math.random() * i * 2;
  // mesh.position.z = Math.random() * i * 2;
  scene.add(mesh);
}

var tw = { x: 1000 }

TweenMax.to(tw, 5, {x: 1500, repeat: -1, yoyo: true, ease: TweenMax.Expo.easeOut })


export default ({
  // lowPulse
}) => {

  let lowPulse = tw.x

  mesh.material.uniforms.mouse.value.set(lowPulse, lowPulse);

  mesh.scale.x = lowPulse * 0.02;
  mesh.scale.y = lowPulse * 0.02;
  mesh.scale.z = lowPulse * 0.02;

  mesh.rotation.y = lowPulse * 0.0008;
  mesh.rotation.x = lowPulse * 0.0008;
  mesh.rotation.z = lowPulse * 0.0008;

  mesh.material.uniforms.time.value = lowPulse * 0.1;
};



