// var NEWTHREE = require('three')
import { scene, camera, renderer } from 'objects/common/scene';
import HorizontalBlur from 'materials/horizontalBlur';
import VerticalBlur from 'materials/verticalBlur';
import RBGShift from 'materials/rgbBlur';

// console.log(NEWTHREE)

const renderPass = new THREE.RenderPass( scene, camera );

// const kaleidoPass = KaleidoShader();
const hBlurPass = HorizontalBlur;
const vBlurPass = VerticalBlur;
const rgbPass = RBGShift;

const composer = new THREE.EffectComposer(renderer);
composer.addPass(renderPass);
composer.addPass(HorizontalBlur);
composer.addPass(VerticalBlur);
composer.addPass(RBGShift);

rgbPass.renderToScreen = true;

export default ({
  lowPulse
}) => {


  // camera.position.z = pulseValue * 100
  // camera.position.y += pulseValue * 0.000000001
  // camera.position.x += pulseValue * 0.000000001

  hBlurPass.uniforms.h.value = lowPulse * 0.0000005;
  vBlurPass.uniforms.v.value = lowPulse * 0.0000005;
  rgbPass.uniforms.amount.value = lowPulse * 0.00001;
  rgbPass.uniforms.angle.value = lowPulse * 0.01;

  composer.render(0.1);
  renderer.render(scene, camera);
};



